@import '../../__data__/style/colors.module.css';
@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';

.form {
    max-width: 350px;
    padding: var(--offset-md);
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    padding-top: 132px;
}

.form > .formTitle {
    color: var(--green-4);
    font-size: 24px;
    margin: 0 auto;
}

.form .buttonOffset {
    margin-top: var(--offset-md);
}
.form .or {
    display: flex;
    margin-top: var(--offset-md);
    align-items: center;
}

.form .orLine {
    display: inline-block;
    height: 1px;
    background-color: var(--black);
    flex: 1 1 auto;
}

.form .orText {
    display: inline-block;
    color: var(--black);
    margin: 0 var(--offset-md);
}

.form .register {
    min-height: 40px;
    border-radius: 3px;
    letter-spacing: 1.05px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    padding: 10px 0;
    background-color: var(--green-2);
    color: var(--green-4);
    margin-top: var(--offset-md);
}

.form .register:hover {
    outline: 1px solid var(--green-4);
    box-shadow: 0 0 3pt 2pt var(--green-4);
}

.form .register:focus {
    outline: 1px solid var(--green-4);
    box-shadow: 0 0 3pt 2pt var(--green-4);
}

@media screen and (min-width: 732px) and (max-width: 1055.98px) {
    .form {
        padding-top: 104px;
    }
}

@media screen and (max-width: 731.98px) {
    .form {
        padding: var(--offset-md) var(--offset-sm);
        padding-top: 84px;
    }
}
